.button {
    font: inherit;
    padding: .5rem 1.5rem;
    border: none;
    border-radius: 4px;
    background: rgb(221, 115, 54);
    color: white;
    cursor: pointer;
    text-decoration: none;
    display: inline-block;
    transition: all 250ms;
}

.button:hover, .button:active {
    background: #cec220;
    border-color: #cec220;
}

.button--inverse {
    background: transparent;
    color: #ff0055;
}

.button--inverse:hover, .button--inverse:active {
    color: white;
    background: #ff0055;
}

.button--danger {
    background: #830000;
    border-color: #830000;
}

.button--danger:hover, .button--danger:active {
    background: #f34343;
    border-color: #f34343;
}

.button:disabled, .button:hover:disabled, .button:active:disabled {
    background: #ccc;
    color: #979797;
    border-color: #ccc;
    cursor: not-allowed;
}

.button--small {
    font-size: .8rem;
}

.button--big {
    font-size: 1.5rem;
}

/* rework this file */
.btn--blue {
    background: #2a4485;
}

.btn--blue:hover {
    background: #7777dd;
}

.btn--red {
    background: #cb1e1e;
